// WelcomeSection View - Pure presentation component with scroll morphing

import React, { useEffect, useRef, useState, useCallback } from 'react';
import { initializeScene } from './WelcomeSection.controller';
import './WelcomeSection.css';

const WelcomeSection: React.FC = () => {
  const asciiContainerRef = useRef<HTMLDivElement>(null);
  const sectionRef = useRef<HTMLElement>(null);
  const [scrollProgress, setScrollProgress] = useState(0);

  // Initialize Three.js scene
  useEffect(() => {
    if (!asciiContainerRef.current) return;

    const { cleanup } = initializeScene(asciiContainerRef.current);

    return cleanup;
  }, []);

  // Handle scroll-driven morph animation
  const handleScroll = useCallback(() => {
    const section = sectionRef.current;
    if (!section) return;

    // Find the scroll container (.App)
    const scrollContainer = section.closest('.App');
    if (!scrollContainer) return;

    const scrollTop = scrollContainer.scrollTop;
    const sectionHeight = section.offsetHeight;

    // Calculate progress: 0 at top, 1 when scrolled past section
    const rawProgress = scrollTop / (sectionHeight * 0.7); // Complete morph at 70% scroll
    const progress = Math.min(Math.max(rawProgress, 0), 1);

    setScrollProgress(progress);
  }, []);

  useEffect(() => {
    const section = sectionRef.current;
    if (!section) return;

    const scrollContainer = section.closest('.App');
    if (!scrollContainer) return;

    handleScroll();
    scrollContainer.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      scrollContainer.removeEventListener('scroll', handleScroll);
    };
  }, [handleScroll]);

  const isCompact = scrollProgress >= 0.95;

  // Calculate interpolated values
  const hexScale = 1 - scrollProgress * 0.85; // Scale from 1 to 0.15
  const nameScale = 1 - scrollProgress * 0.5; // Scale from 1 to 0.5
  const subtitleOpacity = 1 - scrollProgress * 2; // Fade out by 50% scroll
  const scrollIndicatorOpacity = 1 - scrollProgress * 3; // Fade out quickly

  return (
    <>
      <section
        className={`welcome-section ${isCompact ? 'is-compact' : ''}`}
        ref={sectionRef}
        style={{
          '--scroll-progress': scrollProgress,
          '--hex-scale': hexScale,
          '--name-scale': nameScale,
          '--subtitle-opacity': Math.max(subtitleOpacity, 0),
          '--indicator-opacity': Math.max(scrollIndicatorOpacity, 0),
        } as React.CSSProperties}
      >
        <div className="welcome-content">
          <div className="ascii-container" ref={asciiContainerRef} />
          <div className="name-container">
            <h1 className="welcome-name">Brandon Sabino</h1>
            <p className="welcome-subtitle">Frontend Developer</p>
          </div>
        </div>
        <div className="scroll-indicator">
          <span>Scroll</span>
          <div className="scroll-arrow" />
        </div>
      </section>

      {/* Sticky header that appears after morph */}
      <header className={`site-header ${isCompact ? 'is-visible' : ''}`}>
        <div className="header-hex" />
        <h1 className="header-name">Brandon Sabino</h1>

        <nav className="header-social">
          <a
            href="https://linkedin.com/in/brandon-sabino"
            target="_blank"
            rel="noopener noreferrer"
            className="social-link"
            aria-label="LinkedIn"
          >
            <svg viewBox="0 0 24 24" fill="currentColor" className="social-icon">
              <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
            </svg>
          </a>
          <a
            href="https://github.com/brandonsabino"
            target="_blank"
            rel="noopener noreferrer"
            className="social-link"
            aria-label="GitHub"
          >
            <svg viewBox="0 0 24 24" fill="currentColor" className="social-icon">
              <path d="M12 0C5.374 0 0 5.373 0 12c0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23A11.509 11.509 0 0112 5.803c1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576C20.566 21.797 24 17.3 24 12c0-6.627-5.373-12-12-12z"/>
            </svg>
          </a>
          <a
            href="mailto:brandonsabino.dev@gmail.com"
            className="social-link"
            aria-label="Email"
          >
            <svg viewBox="0 0 24 24" fill="currentColor" className="social-icon">
              <path d="M24 5.457v13.909c0 .904-.732 1.636-1.636 1.636h-3.819V11.73L12 16.64l-6.545-4.91v9.273H1.636A1.636 1.636 0 010 19.366V5.457c0-2.023 2.309-3.178 3.927-1.964L5.455 4.64 12 9.548l6.545-4.91 1.528-1.145C21.69 2.28 24 3.434 24 5.457z"/>
            </svg>
          </a>
        </nav>
      </header>
    </>
  );
};

export default WelcomeSection;
